<template>
  <panel>
    <div class="vx-row mb-6">
      <div class="centerx vx-col sm:w-2/3 w-full" v-if="form.ref_id !== null">
        <vs-radio v-model="form.type" vs-value="ต่อพ่วง">ต่อพ่วง</vs-radio>
        &nbsp;

        <vs-radio v-model="form.type" vs-value="อะไหล่">อะไหล่</vs-radio>
        &nbsp;

        <vs-radio v-model="form.type" vs-value="ตัวรถ">ตัวรถ</vs-radio>
      </div>
      <div class="vx-col w-full">
        <form-input
          label="ชื่อ (TH)"
          v-model="form.name_th"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>
    <div class="vx-row mb-6">
      <div class="vx-col w-full"></div>
      <div class="vx-col w-full">
        <form-input
          label="ชื่อ (EN)"
          v-model="form.name_en"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>
    <div class="vx-row mb-6">
      <div class="vx-col w-full"></div>
      <div class="vx-col w-full">
        <form-input
          v-if="form.ref_id !== null"
          label="ราคาขายปลีก"
          v-model="form.retail_price"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>

    <div class="vx-row mb-6"></div>

    <div class="vx-row">
      <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
        <vs-button class="mr-3" @click="openAlert()">บันทึก</vs-button>
        <router-link to="/product/checkWarehouse">
          <vs-button color="danger" type="filled">ยกเลิก</vs-button>
        </router-link>
      </vs-col>
    </div>
  </panel>
</template>

<script>

export default {

  data () {
    return {
      form: {
        type: '',
        retail_price: 'ss'
      }
    }
  },
  methods: {
    async openAlert () {
      this.clickSubmit()
    },

    clickSubmit () {
      if (!this.form.id) {
        this.$emit('clickSubmit', this.form)
        this.$vs.dialog({
          type: 'confirm',
          color: 'success',
          title: 'คุณต้องการเพิ่มข้อมูลหรือไม่',
          text: `เพิ่มข้อมูลสินค้า  ${this.form.name_th}`,
          acceptText: 'ยืนยัน',
          cancelText: 'ยกเลิก',
          accept: this.acceptAdd
        })
        // this.insertNode()
        console.log('add')
      } else {
        this.$emit('clickSubmit', this.form)
        this.$vs.dialog({
          type: 'confirm',
          color: 'success',
          title: 'คุณต้องการแก้ไขข้อมูลตั่งค่าหรือไม่',
          text: `เพิ่มข้อมูลสินค้า  ${this.form.name_th}`,
          acceptText: 'ยืนยัน',
          cancelText: 'ยกเลิก',
          accept: this.acceptUpdate
        })
        this.updateNode()
        console.log('edit')
      }
    },

    async acceptAdd () {
      const resp = await this.$http.post(
        '/api/setting/master/product',
        this.form
      )
      console.log(resp)
      await this.$vs.notify({
        color: 'success',
        title: 'ยืนยันการเพิ่มข้อมูล',
        text: 'เพิ่มข้อมูลสำเร็จ'
      })
      this.$emit('reload')
    },
    async acceptUpdate () {
      const resp = await this.$http.patch(
        `/api/setting/master/product/${this.form}`,
        this.form
      )
      console.log(resp)
      await this.$vs.notify({
        color: 'success',
        title: 'ยืนยันการแก้ไขข้อมูลข้อมูล',
        text: 'แก้ไขข้อมูลสำเร็จ'
      })
      this.$emit('reload')
    }
  }
}
</script>

