<template>
  <vx-card>
    <div class="vx-row">
      <div class="vx-col w-4/12">
        <custom-tree
          ref="tree"
          @onClick="onClick"
          @onDelete="onDelete"
          url="/api/setting/master/product"
        ></custom-tree>
      </div>
      <div class="vx-col w-8/12">
        <setting-form
          v-show="openform"
          ref="form"
          @clickSubmit="clickSubmit"
          @reload="reload"
        >
        </setting-form>
      </div>
    </div>
  </vx-card>
</template>

<script>
import SettingFrom from './SettingFrom'
export default {
  components: {
    'setting-form': SettingFrom
  },
  data () {
    return {
      clickData: null,
      openform: false
    }
  },
  mounted () {},
  methods: {
    onClick (obj) {
      this.clickData = obj
      this.openform = true
      this.$refs.form.form = obj
    },
    onDelete (node, parent, index) {
      this.node = node
      this.parent = parent
      this.index = index
      console.log(node)
      this.$refs.tree.removeNode(node, parent, index)
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'ต้องการลบรายการนี้หรือไม่ ?',
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก',
        text: `คุณแน่ใจหรือไม่ว่าต้องการลบ ${this.node.name_th}`,
        accept: this.acceptAlert
      })
    },
    clickSubmit (obj) {
      console.log(obj)
      this.$refs.tree.refresh()
    },
    reload () {
      this.$refs.tree.refresh()
    },
    async acceptAlert () {
      try {
        await this.$http.delete(`api/setting/master/product/${this.node.id} `)

        await this.loadData()
      } catch (error) {
        // this.$swal('ลบข้อมูลไม่สำเร็จ', '', 'error')
      }
      this.$vs.notify({
        color: 'success',
        title: 'ลบสำเร็จ',
        text: 'บันทึกข้อมูล'
      })
    }
  }
}
</script>

