<template>
  <vx-card>
    <div class="vx-row">
      <div class="vx-col w-4/12">
        <custom-tree
          ref="tree"
          @onClick="onClick"
          @onDelete="onDelete"
          url="/api/setting/master/customer"
           :col="12"
        ></custom-tree>
      </div>
      <div class="vx-col w-8/12">
      <panel title="ตั้งค่า" v-show="openform">
      <setting-form
        ref="form"
        @clickSubmit="clickSubmit">
        </setting-form>
      </panel>
      </div>
    </div>
  </vx-card>
</template>

<script>
import SettingForm from './MemberSetting/SettingForm'
export default {
  components: {
    'setting-form': SettingForm
  },
  data () {
    return {
      clickData: null,
      openform: null,
      node:'',
      parent:'',
      index:''
    }
  },
  mounted () {
    // console.log('in')
    // this.$http
    //   .get('http://api.iseki.test/api/datatable')
    //   .then((response) => (this.ssr = response))
    // this.$http
    //   .post('http://api.iseki.test/api/datatable', {test:1, name:'Jim'})
    //   .then(function (response) {
    //     console.log(response)
    //   })
  },
  methods: {
    onClick (obj) {
      this.clickData = obj
      this.openform = true
      this.$refs.form.form = obj
    },
    async onDelete (node, parent, index) {
      this.node = node
      this.parent = parent
      this.index = index
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'ต้องการลบรายการนี้หรือไม่ ?',
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก',
        text: `คุณแน่ใจหรือไม่ว่าต้องการลบ ${this.node.name_th}{${this.node.name_en}}`,
        accept: this.deleteAlert
      })
    },
    async deleteAlert () {
      await this.$http.delete(
        `/api/setting/master/customer/${this.node.id}`
      )
      this.$vs.notify({
        color: 'success',
        title: 'ลบข้อมูล',
        text: 'ลบข้อมูลระดับสมาชิกกสำเร็จ'
      })
      this.$refs.tree.removeNode(this.node, this.parent, this.index)
    },
    clickSubmit (obj) {
      console.log(obj)
      this.$refs.tree.refresh()
    }
  }
}
</script>

