<template>
  <vx-card>
    <div class="vx-row">

      <div class="vx-col w-5/12">
        <custom-tree
          ref="tree"
          @onClick="onClick"
          @onDelete="onDelete"
          url="/api/setting/master/user"
        ></custom-tree>
      </div>

      <div class="vx-col w-7/12">
        <panel v-show="open" title="ตั้งค่า">
          <setting-form ref="form"  @clickSubmit="clickSubmit">
          </setting-form>
        </panel>
      </div>

    </div>
  </vx-card>
</template>

<script>
import SettingForm from './SettingForm'
export default {
  components: {
    'setting-form': SettingForm
  },
  data () {
    return {
      clickData: null,
      open: false
    }
  },
  mounted () {
    // console.log('in')
    // this.$http
    //   .get('http://api.iseki.test/api/datatable')
    //   .then((response) => (this.ssr = response))
    // this.$http
    //   .post('http://api.iseki.test/api/datatable', {test:1, name:'Jim'})
    //   .then(function (response) {
    //     console.log(response)
    //   })
  },
  methods: {
    onClick (obj) {
      this.clickData = obj
      this.$refs.form.form = obj
      this.open = true
    },
    onDelete (node, parent, index) {
      this.node = node
      this.parent = parent
      this.index = index
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'คุณต้องการลบข้อมูลใช่หรือไม่',
        text: `ลบข้อมูลพนักงานชื่อ ${this.node.name_th}`,
        accept: this.acceptDelete,
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก'
      })

    },
    async acceptDelete () {
      try {
        await this.$http.delete(`/api/setting/master/user/${this.node.id}`)
        await this.$refs.tree.removeNode(this.node, this.parent, this.index)
        await this.$vs.notify({
          color: 'danger',
          title: 'ยืนยันการลบข้อมูล',
          text: 'ลบข้อมูลสำเร็จ'
        })
      } catch (error) {
        throw error
      }
    },
    clickSubmit (obj) {
      console.log(obj)
      this.$refs.tree.refresh()
    }
  }
}
</script>

