<template>
  <div>
    <div class="vx-row">
      <div class="vx-col sm:w-full">
        <form-input
          label="Thai"
          v-model="form.name_th"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>
    <div class="vx-row">
      <div class="vx-col sm:w-full">
        <form-input
          label="Eng"
          v-model="form.name_en"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>
    <vs-row>
      <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
        <vs-button class="mr-3" @click="clickSubmit()">บันทึก</vs-button>
        <router-link to="/product/checkWarehouse">
          <vs-button color="danger" type="filled">ยกเลิก</vs-button>
        </router-link>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {}
    }
  },
  methods: {
    async clickSubmit () {
      if (!this.form.id) {
        try {
          const result = await this.$validator.validateAll()
          if (result) {
            await this.$vs.dialog({
              type: 'confirm',
              color: 'success',
              title: 'คุณต้องการเพิ่มข้อมูลหรือไม่',
              text: `เพิ่มข้อมูลระดับสมาชิก ${this.form.name_th}(${this.form.name_en})`,
              acceptText: 'ยืนยัน',
              cancelText: 'ยกเลิก',
              accept: this.acceptInsert
            })
          } else {
            await this.$vs.dialog({
              color: 'danger',
              title: 'สถานะการเพิ่มข้อมูล',
              text: 'เพิ่มข้อมูลระดับสมาชิกไม่สำเร็จ',
              acceptText: 'ปิด'
            })
          }
        } catch (error) {
          console.log('err', error)
        }
      } else {
        try {
          const result = await this.$validator.validateAll()
          if (result) {
            this.$vs.dialog({
              type: 'confirm',
              color: 'success',
              title: 'แก้ข้อมูลสำเร็จ',
              text: `แก้ไขข้อมูลระดับสมาชิก  ${this.form.name_th}(${this.form.name_en})`,
              acceptText: 'ยืนยัน',
              cancelText: 'ยกเลิก',
              accept: this.acceptAlert
            })
          } else {
            this.$vs.dialog({
              color: 'danger',
              title: 'แก้ข้อมูลไม่สำเร็จ',
              text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
              acceptText: 'ปิด',
              accept: this.acceptAlertFail
            })
          }
        } catch (error) {
          this.$vs.dialog({
            color: 'danger',
            title: 'แก้ข้อมูลไม่สำเร็จ',
            text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
            acceptText: 'ปิด',
            accept: this.acceptAlertFail
          })
        }
      }
      // this.$emit('clickSubmit', this.form)
    },
    async acceptAlert () {
      await this.$http.patch(
        `/api/setting/master/customer/${this.form.id}`,
        this.form
      )
      this.$vs.notify({
        color: 'success',
        title: 'แก้ไขข้อมูล',
        text: 'แก้ไขข้อมูลระดับสมาชิกกสำเร็จ'
      })
      this.$emit(
        'clickSubmit',
        (this.form.name_th = ''),
        (this.form.name_en = '')
      )
    },
    async acceptAlertFail () {
      this.$vs.notify({
        color: 'danger',
        title: 'แก้ไขข้อมูลไม่สำเร็จ',
        text: 'แก้ไขข้อมูลระดับสมาชิกไม่สำเร็จ'
      })
    },
    async acceptInsert () {
      try {
        await this.$http.post('/api/setting/master/customer/', this.form)
        await this.$vs.notify({
          color: 'success',
          title: 'ยืนยันการเพิ่มข้อมูล',
          text: 'เพิ่มข้อมูลสำเร็จ'
        })
        this.$emit(
          'clickSubmit',
          (this.form.name_th = ''),
          (this.form.name_en = '')
        )
      } catch (error) {
        throw error
      }
    }
  }
}
</script>

