<template>
  <div>
    <!-- <h1>Test</h1> -->
    <div class="vx-row">
      <div class="vx-col w-full">
        <form-input
          label="Thai"
          v-model="form.name_th"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>
    <div class="vx-row">
      <div class="vx-col w-full">
        <form-input
          label="Eng"
          v-model="form.name_en"
          v-validate="`required`"
          data-vv-as="Title"
          :col="12"
        />
      </div>
    </div>

    <div class="vx-row mb-6 my-5" v-if="this.form.ref_id !== null">
      <div class="vx-col sm:w-2/3 w-full">
        <vs-radio v-model="form.checkLogin" vs-name="radios1" vs-value="ล็อกอิน"
      >ล็อกอิน</vs-radio
    >&nbsp;
    <vs-radio v-model="form.checkLogin" vs-name="radios2" vs-value="ไม่ล็อกอิน"
      >ไม่ล็อกอิน</vs-radio
    >
      </div>
    </div>


    <vs-row>
      <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
        <vs-button class="mr-3" @click="openConfirm()">บันทึก</vs-button>
        <router-link to="/product/checkWarehouse">
          <vs-button color="danger" type="filled">ยกเลิก</vs-button>
        </router-link>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {}
    }
  },
  methods: {
    async openConfirm () {
      if (!this.form.id) {
        try {
          const result = await this.$validator.validateAll()
          if (result) {
            await this.$vs.dialog({
              type: 'confirm',
              color: 'success',
              title: 'คุณต้องการเพิ่มข้อมูลหรือไม่',
              text: `เพิ่มข้อมูลชื่อไทย ${this.form.name_th} ชื่ออังกฤษ ${this.form.name_en}`,
              acceptText: 'ยืนยัน',
              cancelText: 'ยกเลิก',
              accept: this.clickSubmitInsert
            })
          } else {
            await this.$vs.dialog({
              color: 'danger',
              title: 'สถานะการเพิ่มข้อมูล',
              text: 'เพิ่มข้อมูลพนักงานไม่สำเร็จ',
              acceptText: 'ปิด'
            })
          }
        } catch (error) {
          console.log('err', error)
        }
      } else {
        try {
          const result = await this.$validator.validateAll()
          if (result) {
            await this.$vs.dialog({
              type: 'confirm',
              color: 'success',
              title: 'คุณต้องการแก้ไขข้อมูลหรือไม่',
              text: `แก้ไขข้อมูลชื่อไทย ${this.form.name_th} ชื่ออังกฤษ ${this.form.name_en}`,
              acceptText: 'ยืนยัน',
              cancelText: 'ยกเลิก',
              accept: this.clickSubmitUpdate
            })
          } else {
            await this.$vs.dialog({
              color: 'danger',
              title: 'สถานะการเพิ่มข้อมูล',
              text: 'เพิ่มข้อมูลพนักงานไม่สำเร็จ',
              acceptText: 'ปิด'
            })
          }
        } catch (error) {
          console.log('err', error)
        }
      }
    },
    async clickSubmitInsert () {
      try {
        await this.$http.post('/api/setting/master/user/', this.form)

        await this.$vs.notify({
          color: 'success',
          title: 'ยืนยันการเพิ่มข้อมูล',
          text: 'เพิ่มข้อมูลสำเร็จ'
        })

        await this.$emit('clickSubmit', this.form)
      } catch (error) {
        throw error
      }
    },
    async clickSubmitUpdate () {
      try {
        await this.$http.put(
          `/api/setting/master/user/${this.form.id}`,
          this.form
        )
        await this.$vs.notify({
          color: 'success',
          title: 'ยืนยันการแก้ไขข้อมูล',
          text: 'แก้ไขข้อมูลสำเร็จ'
        })

        await this.$emit('clickSubmit', this.form)
      } catch (error) {
        throw error
      }
    }
  }
}
</script>

